import React from 'react';

class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { 
      hasError: false, 
      error: null, 
      errorInfo: null 
    };
  }

  static getDerivedStateFromError(error) {
    // Êõ¥Êñ∞ state ‰Ωø‰∏ã‰∏ÄÊ¨°Ê∏≤ÊüìËÉΩÂ§üÊòæÁ§∫ÈôçÁ∫ßÂêéÁöÑ UI
    return { hasError: true };
  }

  componentDidCatch(error, errorInfo) {
    // ‰Ω†ÂèØ‰ª•Â∞ÜÈîôËØØÊó•Âøó‰∏äÊä•ÁªôÊúçÂä°Âô®
    console.error('üî• ÈîôËØØËæπÁïåÊçïËé∑Âà∞ÈîôËØØ:', error);
    console.error('üî• ÈîôËØØ‰ø°ÊÅØ:', errorInfo);
    
    this.setState({
      error: error,
      errorInfo: errorInfo
    });
  }

  render() {
    if (this.state.hasError) {
      // ‰Ω†ÂèØ‰ª•Ëá™ÂÆö‰πâÈôçÁ∫ßÂêéÁöÑ UI Âπ∂Ê∏≤Êüì
      return (
        <div style={{
          background: '#fff2f0',
          border: '2px solid #ff4d4f',
          borderRadius: '8px',
          padding: '20px',
          margin: '20px 0'
        }}>
          <h2 style={{ color: '#ff4d4f', margin: '0 0 10px 0' }}>
            üî• ÁªÑ‰ª∂ÂèëÁîüÈîôËØØÔºÅ
          </h2>
          
          <div style={{ 
            background: '#f5f5f5', 
            padding: '15px', 
            borderRadius: '4px',
            marginBottom: '15px'
          }}>
            <h3 style={{ margin: '0 0 10px 0', color: '#333' }}>
              ÈîôËØØËØ¶ÊÉÖÔºö
            </h3>
            <p style={{ margin: '5px 0', color: '#666' }}>
              <strong>ÈîôËØØÂêçÁß∞:</strong> {this.state.error?.name}
            </p>
            <p style={{ margin: '5px 0', color: '#666' }}>
              <strong>ÈîôËØØÊ∂àÊÅØ:</strong> {this.state.error?.message}
            </p>
            <p style={{ margin: '5px 0', color: '#666' }}>
              <strong>ÈîôËØØÂ†ÜÊ†à:</strong>
            </p>
            <pre style={{ 
              background: '#fff', 
              padding: '10px', 
              borderRadius: '4px',
              fontSize: '12px',
              overflow: 'auto',
              maxHeight: '200px'
            }}>
              {this.state.error?.stack}
            </pre>
          </div>

          <div style={{ 
            background: '#e6f7ff', 
            padding: '15px', 
            borderRadius: '4px',
            marginBottom: '15px'
          }}>
            <h3 style={{ margin: '0 0 10px 0', color: '#1890ff' }}>
              ÁªÑ‰ª∂Â†ÜÊ†à‰ø°ÊÅØÔºö
            </h3>
            <pre style={{ 
              background: '#fff', 
              padding: '10px', 
              borderRadius: '4px',
              fontSize: '12px',
              overflow: 'auto',
              maxHeight: '200px'
            }}>
              {this.state.errorInfo?.componentStack}
            </pre>
          </div>

          <button 
            onClick={() => {
              this.setState({ hasError: false, error: null, errorInfo: null });
              window.location.reload();
            }}
            style={{
              background: '#1890ff',
              color: 'white',
              border: 'none',
              padding: '10px 20px',
              borderRadius: '4px',
              cursor: 'pointer',
              fontSize: '14px'
            }}
          >
            üîÑ ÈáçÊñ∞Âä†ËΩΩÈ°µÈù¢
          </button>
        </div>
      );
    }

    return this.props.children;
  }
}

export default ErrorBoundary; 