import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { 
  Card, 
  Form, 
  Input, 
  Button, 
  Upload, 
  message, 
  Typography, 
  Divider, 
  Space, 
  Image 
} from 'antd';
import { 
  UploadOutlined,
  EyeOutlined,
  DeleteOutlined,
  SaveOutlined
} from '@ant-design/icons';
import { getPaymentConfig, updatePaymentConfig } from '../../store/slices/paymentConfigSlice';

const { Title, Text } = Typography;

const AdminPaymentConfig = () => {
  const dispatch = useDispatch();
  const { config, loading } = useSelector((state) => state.paymentConfig);
  const [form] = Form.useForm();
  const [alipayQRCode, setAlipayQRCode] = useState('');
  const [cryptoQRCode, setCryptoQRCode] = useState('');

  useEffect(() => {
    dispatch(getPaymentConfig());
  }, [dispatch]);

  useEffect(() => {
    if (config) {
      form.setFieldsValue({
        alipay_account: config.alipay_account,
        alipay_surname: config.alipay_surname,
        crypto_chain_name: config.crypto_chain_name,
        crypto_address: config.crypto_address
      });
      setAlipayQRCode(config.alipay_qr_code);
      setCryptoQRCode(config.crypto_qr_code);
    }
  }, [config, form]);

  const handleSave = async (values) => {
    try {
      const configData = {
        alipay_account: values.alipay_account,
        alipay_surname: values.alipay_surname,
        alipay_qr_code: alipayQRCode, // ÂåÖÂê´‰∫åÁª¥Á†ÅÂõæÁâáÊï∞ÊçÆ
        crypto_chain_name: values.crypto_chain_name,
        crypto_address: values.crypto_address,
        crypto_qr_code: cryptoQRCode // ÂåÖÂê´‰∫åÁª¥Á†ÅÂõæÁâáÊï∞ÊçÆ
      };

      await dispatch(updatePaymentConfig(configData)).unwrap();
      message.success('ÈÖçÁΩÆ‰øùÂ≠òÊàêÂäü');
    } catch (error) {
      message.error('ÈÖçÁΩÆ‰øùÂ≠òÂ§±Ë¥•');
    }
  };

  const handleUpload = (file, type) => {
    const isImage = file.type.startsWith('image/');
    if (!isImage) {
      message.error('Âè™ËÉΩ‰∏ä‰º†ÂõæÁâáÊñá‰ª∂ÔºÅ');
      return false;
    }
    
    const isLt10M = file.size / 1024 / 1024 < 10;
    if (!isLt10M) {
      message.error('ÂõæÁâáÂ§ßÂ∞è‰∏çËÉΩË∂ÖËøá10MBÔºÅ');
      return false;
    }

    const formData = new FormData();
    formData.append('qr_code', file);
    formData.append('type', type);

    // Ê®°Êãü‰∏ä‰º†
    const reader = new FileReader();
    reader.onload = (e) => {
      if (type === 'alipay') {
        setAlipayQRCode(e.target.result);
      } else {
        setCryptoQRCode(e.target.result);
      }
    };
    reader.readAsDataURL(file);

    return false; // ÈòªÊ≠¢Ëá™Âä®‰∏ä‰º†
  };

  const handlePreview = (imageUrl) => {
    // ÂÆûÁé∞ÂõæÁâáÈ¢ÑËßàÈÄªËæë
    console.log('È¢ÑËßàÂõæÁâá:', imageUrl);
  };

  const handleRemove = (type) => {
    if (type === 'alipay') {
      setAlipayQRCode('');
    } else {
      setCryptoQRCode('');
    }
  };

  const alipayUploadProps = {
    beforeUpload: (file) => handleUpload(file, 'alipay'),
    showUploadList: false,
  };

  const cryptoUploadProps = {
    beforeUpload: (file) => handleUpload(file, 'crypto'),
    showUploadList: false,
  };

  return (
    <div>
      <Title level={2}>Êî∂Ê¨æÈÖçÁΩÆ</Title>

      <Form
        form={form}
        layout="vertical"
        onFinish={handleSave}
      >
        {/* ÊîØ‰ªòÂÆùÊî∂Ê¨æÈÖçÁΩÆ */}
        <Card title="ÊîØ‰ªòÂÆùÊî∂Ê¨æÈÖçÁΩÆ" style={{ marginBottom: 24 }}>
          <Space direction="vertical" style={{ width: '100%' }}>
            <Form.Item
              label="ÊîØ‰ªòÂÆùË¥¶Âè∑"
              name="alipay_account"
              rules={[{ required: true, message: 'ËØ∑ËæìÂÖ•ÊîØ‰ªòÂÆùË¥¶Âè∑' }]}
            >
              <Input placeholder="ËØ∑ËæìÂÖ•ÊîØ‰ªòÂÆùË¥¶Âè∑" />
            </Form.Item>
            
            <Form.Item
              label="Êî∂Ê¨æ‰∫∫ÂßìÊ∞è"
              name="alipay_surname"
              rules={[{ required: true, message: 'ËØ∑ËæìÂÖ•Êî∂Ê¨æ‰∫∫ÂßìÊ∞è' }]}
            >
              <Input placeholder="ËØ∑ËæìÂÖ•Êî∂Ê¨æ‰∫∫ÂßìÊ∞è" />
            </Form.Item>
            
            <Form.Item label="ÊîØ‰ªòÂÆùÊî∂Ê¨æÁ†Å">
              <Space direction="vertical" style={{ width: '100%' }}>
                {alipayQRCode ? (
                  <div style={{ display: 'flex', alignItems: 'center', gap: 16 }}>
                    <Image
                      width={100}
                      height={100}
                      src={alipayQRCode}
                      style={{ objectFit: 'cover', borderRadius: 8 }}
                    />
                    <Space>
                      <Button 
                        type="link" 
                        icon={<EyeOutlined />}
                        onClick={() => handlePreview(alipayQRCode)}
                      >
                        È¢ÑËßà
                      </Button>
                      <Button 
                        type="link" 
                        danger
                        icon={<DeleteOutlined />}
                        onClick={() => handleRemove('alipay')}
                      >
                        Âà†Èô§
                      </Button>
                    </Space>
                  </div>
                ) : (
                  <Upload {...alipayUploadProps}>
                    <Button icon={<UploadOutlined />}>‰∏ä‰º†Êî∂Ê¨æÁ†Å</Button>
                  </Upload>
                )}
                <Text type="secondary">ÊîØÊåÅ JPG„ÄÅPNG„ÄÅGIF„ÄÅWebP Ê†ºÂºèÔºåÊúÄÂ§ß 10MB</Text>
              </Space>
            </Form.Item>
          </Space>
        </Card>

        <Divider />

        {/* Á∫ø‰∏äÂú∞ÂùÄÊî∂Ê¨æÈÖçÁΩÆ */}
        <Card title="Á∫ø‰∏äÂú∞ÂùÄÊî∂Ê¨æÈÖçÁΩÆ" style={{ marginBottom: 24 }}>
          <Space direction="vertical" style={{ width: '100%' }}>
            <Form.Item
              label="ÈìæÂêç"
              name="crypto_chain_name"
              rules={[{ required: true, message: 'ËØ∑ËæìÂÖ•ÈìæÂêç' }]}
            >
              <Input placeholder="Â¶ÇÔºöTRC10/TRC20" />
            </Form.Item>
            
            <Form.Item
              label="Êî∂Ê¨æÂú∞ÂùÄ"
              name="crypto_address"
              rules={[{ required: true, message: 'ËØ∑ËæìÂÖ•Êî∂Ê¨æÂú∞ÂùÄ' }]}
            >
              <Input.TextArea 
                placeholder="ËØ∑ËæìÂÖ•Êî∂Ê¨æÂú∞ÂùÄ" 
                rows={3}
              />
            </Form.Item>
            
            <Form.Item label="Á∫ø‰∏äÊî∂Ê¨æÁ†Å">
              <Space direction="vertical" style={{ width: '100%' }}>
                {cryptoQRCode ? (
                  <div style={{ display: 'flex', alignItems: 'center', gap: 16 }}>
                    <Image
                      width={100}
                      height={100}
                      src={cryptoQRCode}
                      style={{ objectFit: 'cover', borderRadius: 8 }}
                    />
                    <Space>
                      <Button 
                        type="link" 
                        icon={<EyeOutlined />}
                        onClick={() => handlePreview(cryptoQRCode)}
                      >
                        È¢ÑËßà
                      </Button>
                      <Button 
                        type="link" 
                        danger
                        icon={<DeleteOutlined />}
                        onClick={() => handleRemove('crypto')}
                      >
                        Âà†Èô§
                      </Button>
                    </Space>
                  </div>
                ) : (
                  <Upload {...cryptoUploadProps}>
                    <Button icon={<UploadOutlined />}>‰∏ä‰º†Êî∂Ê¨æÁ†Å</Button>
                  </Upload>
                )}
                <Text type="secondary">ÊîØÊåÅ JPG„ÄÅPNG„ÄÅGIF„ÄÅWebP Ê†ºÂºèÔºåÊúÄÂ§ß 10MB</Text>
              </Space>
            </Form.Item>
          </Space>
        </Card>

        <Form.Item>
          <Button 
            type="primary" 
            htmlType="submit" 
            loading={loading}
            icon={<SaveOutlined />}
          >
            ‰øùÂ≠òÈÖçÁΩÆ
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default AdminPaymentConfig; 