import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { 
  Table, 
  Card, 
  Tag, 
  message, 
  Typography,
  Select,
  DatePicker,
  Form,
  Row,
  Col,
  Input,
  Space,
  Button
} from 'antd';
import { 
  SearchOutlined,
  ExportOutlined
} from '@ant-design/icons';
import dayjs from 'dayjs';
import { getCustomers } from '../../store/slices/adminSlice';

const { Title } = Typography;
const { Option } = Select;
const { RangePicker } = DatePicker;

const AdminCustomers = () => {
  const dispatch = useDispatch();
  const { customers, loading } = useSelector((state) => state.admin);
  const [form] = Form.useForm();
  // ÁßªÈô§Êú™‰ΩøÁî®ÁöÑÁä∂ÊÄÅÂèòÈáè‰ª•ÈÅøÂÖçË≠¶Âëä

  // ÂàùÂßãÂåñÊï∞ÊçÆ
  useEffect(() => {
    dispatch(getCustomers());
  }, [dispatch]);

  // ÁßªÈô§Êú™‰ΩøÁî®ÁöÑLoadingSkeletonÁªÑ‰ª∂

  // Â§ÑÁêÜÊêúÁ¥¢
  const handleSearch = () => {
    const searchValues = form.getFieldsValue();
    console.log('ÊêúÁ¥¢Êù°‰ª∂:', searchValues);
    
    // ËΩ¨Êç¢ÂèÇÊï∞Âêç‰ª•ÂåπÈÖçÂêéÁ´ØAPI
    const apiParams = {
      customer_wechat: searchValues.customer_wechat,
      sales_wechat: searchValues.sales_wechat,
      is_reminded: searchValues.remind_status,
      start_date: searchValues.date_range?.[0]?.format('YYYY-MM-DD'),
      end_date: searchValues.date_range?.[1]?.format('YYYY-MM-DD')
    };
    
    // ÁßªÈô§Á©∫ÂÄº
    Object.keys(apiParams).forEach(key => {
      if (!apiParams[key]) {
        delete apiParams[key];
      }
    });
    
    dispatch(getCustomers(apiParams));
    message.success('ÊêúÁ¥¢ÂÆåÊàê');
  };

  // ÈáçÁΩÆÊêúÁ¥¢
  const handleReset = () => {
    form.resetFields();
    dispatch(getCustomers());
  };



  // Ë°®Ê†ºÂàóÂÆö‰πâ
  const columns = [
    {
      title: 'ÂÆ¢Êà∑ÂæÆ‰ø°Âè∑',
      dataIndex: 'customer_wechat',
      key: 'customer_wechat',
      width: 120,
    },
    {
      title: 'TradingViewÁî®Êà∑',
      dataIndex: 'tradingview_username',
      key: 'tradingview_username',
      width: 150,
    },
    {
      title: 'ÈîÄÂîÆÂæÆ‰ø°Âè∑',
      dataIndex: 'sales_wechat',
      key: 'sales_wechat',
      width: 120,
    },
    {
      title: 'ÊÄªËÆ¢ÂçïÊï∞',
      dataIndex: 'total_orders',
      key: 'total_orders',
      width: 100,
    },
    {
      title: 'ÊÄªÈáëÈ¢ù',
      dataIndex: 'total_amount',
      key: 'total_amount',
      width: 100,
      render: (amount) => `$${amount}`,
    },
    {
      title: 'Ëøî‰Ω£ÈáëÈ¢ù',
      dataIndex: 'commission_amount',
      key: 'commission_amount',
      width: 100,
      render: (amount) => `$${parseFloat(amount || 0).toFixed(2)}`,
    },
    {
      title: 'Âà∞ÊúüÊó∂Èó¥',
      dataIndex: 'expiry_date',
      key: 'expiry_date',
      width: 150,
      render: (date) => dayjs(date).format('YYYY-MM-DD HH:mm'),
    },
    {
      title: 'ÂÇ¨ÂçïÁä∂ÊÄÅ',
      dataIndex: 'is_reminded',
      key: 'is_reminded',
      width: 100,
      render: (isReminded) => {
        const statusMap = {
          false: { text: 'Êú™ÂÇ¨Âçï', color: 'orange' },
          true: { text: 'Â∑≤ÂÇ¨Âçï', color: 'green' }
        };
        const statusInfo = statusMap[isReminded] || { text: isReminded ? 'Â∑≤ÂÇ¨Âçï' : 'Êú™ÂÇ¨Âçï', color: 'default' };
        return <Tag color={statusInfo.color}>{statusInfo.text}</Tag>;
      }
    },

  ];

  return (
    <div>
      <Title level={2}>ÂÆ¢Êà∑ÁÆ°ÁêÜ</Title>

      {/* ÊêúÁ¥¢Ë°®Âçï */}
      <Card style={{ marginBottom: 16 }}>
        <Form form={form} layout="inline">
          <Row gutter={[16, 16]} style={{ width: '100%' }}>
            <Col xs={24} sm={12} md={6}>
              <Form.Item name="customer_wechat" label="ÂÆ¢Êà∑ÂæÆ‰ø°">
                <Input placeholder="ËØ∑ËæìÂÖ•ÂÆ¢Êà∑ÂæÆ‰ø°" />
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item name="sales_wechat" label="ÈîÄÂîÆÂæÆ‰ø°Âè∑">
                <Input placeholder="ËØ∑ËæìÂÖ•ÈîÄÂîÆÂæÆ‰ø°Âè∑" />
              </Form.Item>
            </Col>

            <Col xs={24} sm={12} md={6}>
              <Form.Item name="remind_status" label="ÂÇ¨ÂçïÁä∂ÊÄÅ">
                <Select placeholder="ËØ∑ÈÄâÊã©Áä∂ÊÄÅ" allowClear>
                  <Option value="false">Êú™ÂÇ¨Âçï</Option>
                  <Option value="true">Â∑≤ÂÇ¨Âçï</Option>
                </Select>
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item name="date_range" label="Âà∞ÊúüÊó∂Èó¥">
                <RangePicker style={{ width: '100%' }} />
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item name="config_confirmed_filter" label="ÈÖçÁΩÆÁ°ÆËÆ§Áä∂ÊÄÅ">
                <Select placeholder="ÈÄâÊã©ÈÖçÁΩÆÁ°ÆËÆ§Áä∂ÊÄÅ" allowClear>
                  <Option value="all">ÂÖ®ÈÉ®ËÆ¢Âçï</Option>
                  <Option value="confirmed">Â∑≤ÈÖçÁΩÆÁ°ÆËÆ§</Option>
                  <Option value="pending">ÂæÖÈÖçÁΩÆÁ°ÆËÆ§</Option>
                </Select>
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Space>
                <Button type="primary" icon={<SearchOutlined />} onClick={handleSearch}>
                  ÊêúÁ¥¢
                </Button>
                <Button onClick={handleReset}>ÈáçÁΩÆ</Button>
                <Button icon={<ExportOutlined />} onClick={() => {
                  if (!customers || customers.length === 0) {
                    message.warning('ÊöÇÊó†Êï∞ÊçÆÂèØÂØºÂá∫');
                    return;
                  }
                  
                  // ÂáÜÂ§áÂØºÂá∫Êï∞ÊçÆ
                  const exportData = customers.map(customer => ({
                    'ÂÆ¢Êà∑ÂæÆ‰ø°Âè∑': customer.customer_wechat || '',
                    'TradingViewÁî®Êà∑Âêç': customer.tradingview_username || '',
                    'ÈîÄÂîÆÂæÆ‰ø°Âè∑': customer.sales_wechat || '',
                    'ËÆ¢ÂçïÈáëÈ¢ù': customer.amount ? `$${customer.amount}` : '',
                    'ËÆ¢ÂçïÊó∂Èïø': customer.duration_text || customer.duration || '',
                    'Âà∞ÊúüÊó∂Èó¥': customer.expiry_time ? new Date(customer.expiry_time).toLocaleString('zh-CN') : '',
                    'ÂÇ¨ÂçïÁä∂ÊÄÅ': customer.is_reminded ? 'Â∑≤ÂÇ¨Âçï' : 'Êú™ÂÇ¨Âçï',
                    'ËÆ¢ÂçïÁä∂ÊÄÅ': customer.status_text || customer.status || '',
                    'ÂàõÂª∫Êó∂Èó¥': customer.created_at ? new Date(customer.created_at).toLocaleString('zh-CN') : ''
                  }));

                  if (exportData.length === 0) {
                    message.warning('Ê≤°ÊúâÊï∞ÊçÆÂèØÂØºÂá∫');
                    return;
                  }

                  // ÁîüÊàêCSVÂÜÖÂÆπ
                  const headers = Object.keys(exportData[0]);
                  const csvContent = [
                    headers.join(','),
                    ...exportData.map(row => headers.map(header => `"${row[header] || ''}"`).join(','))
                  ].join('\n');

                  // ÂàõÂª∫Âπ∂‰∏ãËΩΩÊñá‰ª∂
                  const blob = new Blob(['\ufeff' + csvContent], { type: 'text/csv;charset=utf-8;' });
                  const link = document.createElement('a');
                  const url = URL.createObjectURL(blob);
                  link.setAttribute('href', url);
                  link.setAttribute('download', `ÂÆ¢Êà∑Êï∞ÊçÆ_${new Date().toISOString().split('T')[0]}.csv`);
                  link.style.visibility = 'hidden';
                  document.body.appendChild(link);
                  link.click();
                  document.body.removeChild(link);

                  message.success('ÂÆ¢Êà∑Êï∞ÊçÆÂØºÂá∫ÊàêÂäü');
                }}>
                  ÂØºÂá∫Êï∞ÊçÆ
                </Button>
              </Space>
            </Col>
          </Row>
        </Form>
      </Card>

      {/* ÂÆ¢Êà∑ÂàóË°® */}
      <Card>
        <Table
          columns={columns}
          dataSource={Array.isArray(customers) ? customers : []}
          rowKey="id"
          pagination={{
            showSizeChanger: true,
            showQuickJumper: true,
            showTotal: (total, range) => `Á¨¨ ${range[0]}-${range[1]} Êù°/ÂÖ± ${total} Êù°`,
          }}
          loading={loading}
          scroll={{ x: 1400 }}
        />
      </Card>




    </div>
  );
};

export default AdminCustomers; 